@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital@1&display=swap');
.aboutbox {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #bbc4b9;
    // background-color: aqua;
}

.about1 {
    width: 75%;
    height: 80%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    // background-color: pink;
}

.slogun {
    width: 80%;
    height: 10%;
    font-family: 'Merriweather', serif;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    font-size: 35px;
    text-shadow: 8px 8px 2px black;
    // background-color: red;
}

.picturebox {
    width: 40%;
    height: 45%;
    background-repeat: no-repeat;
    // background-color: blue;
    display: flex;
    justify-content: center;
    align-items: center;
}

.picture {
    width: 300px;
    height: 300px;
    background-image: url('../../images/about/picture.png');
    background-position: center;
    background-size: 100% 100%;
    border-radius: 50%;
    box-shadow: 0px 0px 30px 0px #000;
}

.record {
    width: 40%;
    height: 45%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    font-family: 'Noto Sans KR', sans-serif;
    // background-color: yellow;
}

.record h2 {
    width: 80%;
    // background-color: black;
}

.chart {
    width: 40%;
    height: 45%;
    overflow: hidden;
    // background-color: yellow;
}

.introduce {
    width: 40%;
    height: 45%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-content: center;
    font-family: 'Noto Sans KR', sans-serif;
    // background-color: blue;
}

.introduce h2 {
    width: 90%;
    font-size: 20px;
    // background-color: aqua;
}