@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

.footer_box {
  width: 100%;
  height: 50px;
  position: absolute;
  left: 0;
  bottom: 0;
  // background-color: pink;
  font-family: 'Bebas Neue', cursive;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer_title {
  width: 500px;
  height: 50px;
  // background-color: gray;
  display: flex;
  align-items: center;
  font-size: 20px;
  margin-left: 50px;
  overflow: hidden;
}

.footer_index {
  width: 200px;
  height: 50px;
  // background-color: gray;
  display: flex;
  margin-right: 50px;
}

.footer_index div {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  // background-color: blue;
}

.footer_index div:hover {
  cursor: pointer;
}